import React, {useEffect, useState} from 'react';
import db from './db/db.js';

function Room({match}) {

    const [question,setQuestion] = useState([]);

    useEffect(() => {
        console.log(match.params.key);
        setQuestion(db(match.params.key));
        console.log(question);
    },[]);

    return (
        <div>
            <h1>{question[0]}번 방</h1>
            {question[2]}
        </div>
    );
}

export default Room;