let data = [
    {
        index : 0,
        key : 'enter',
        q : 'welcome to the great room escape. the next key is "helloworld"'
    },
    {
        index : 1,
        key : 'helloworld',
        q : 'there was 2 people on the boat. the next key is "ubuntu"'
    },
    {
        index : 2,
        key : 'ubuntu',
        q : 'there was 3 people on the bench'
    }
];

export default function(params){

    let result = data.find((value)=>value.key===params);
    return result ? Object.values(result): ['error','','The key is invalid'];
}